export default {
	data() {
		return {
			videoSuccess: false,
			inputText: '',
			appList: [],
			faqList: [],
			videoObj: {},
			downProgress: ''
		}
	},
	computed: {
		isShow() {
			if (this.inputText == "") {
				return false;
			} else {
				return true;
			}
		}
	},
	onLoad() {
		this.getPlatform();
		this.getFAQ();
	},
	onShow() {
		uni.getClipboardData({
			success: (res) => {
				var data = this.trim(res.data)
				if (data && data != this.inputText) {
					uni.showModal({
						title: '要打开粘贴板中的链接吗？',
						content: data,
						cancelText: '取消',
						confirmText: '解析',
						success: (btn) => {
							if (btn.confirm) {
								this.inputText = data;
							}
						},
						fail: () => {
							uni.showToast({
								title: '复制失败',
								mask: false,
								duration: 1500
							});
						}
					});
				}
			}
		});
	},
	methods: {
		clearText() {
			this.inputText = "";
		},
		//去除首尾空格
		trim(s) {
			return s.replace(/(^\s*)|(\s*$)/g, "");
		},
		toRecharge() {
			var loginRes = this.$checkLogin('/pages/index/index', 2);
			if (!loginRes) {
				return false;
			}
			uni.navigateTo({
				url: "/pages/downApp/downApp"
			})
		},
		toCourse() {
			uni.navigateTo({
				url: '/pages/course/course'
			});
		},
		getVideo() {
			var loginRes = this.$checkLogin('/pages/index/index', 2);
			if (!loginRes) {
				return false;
			}
			uni.showLoading({
				title: '解析中',
				mask: true
			});
			this.$uniRequest(
				this.$baseUrl + '/v1/dewater/parseContent', {
					content: this.inputText
				},
				(res) => {
					if (res.data.code == 200) {
						uni.hideLoading();
						var data = res.data.data;
						this.videoObj = data;
						this.downProgress = "保存到本地"
						this.videoSuccess = true;
						uni.showToast({
							title: '解析成功',
							duration: 1500
						});
					} else {
						uni.hideLoading();
						uni.showModal({
							title: '提示',
							content: res.data.msg,
							showCancel: false,
							confirmText: '知道啦'
						});
					}
				}
			)
		},
		downVideo(url) {
			var loginRes = this.$checkLogin('/pages/index/index', 2);
			if (!loginRes) {
				return false;
			}
			if (this.downProgress != "保存到本地") {
				return false;
			}
			var downloadTask = uni.downloadFile({
				url: url,
				success: (res) => {
					if (res.statusCode === 200) {
						uni.saveVideoToPhotosAlbum({
							filePath: res.tempFilePath,
							success: (data) => {
								this.downProgress = "保存成功，请到本地相册查看"
								uni.showToast({
									title: '下载成功',
									mask: false,
									duration: 1500
								});
							},
							fail: (err) => {
								this.downProgress = '保存到本地'
								uni.showToast({
									title: '保存失败',
									mask: false,
									icon: 'none',
									duration: 1500
								});
							}
						});

					}
				}
			});
			downloadTask.onProgressUpdate((res) => {
				this.downProgress = res.progress + '%';
			});
		},
		getPlatform() {
			this.$uniRequest(
				this.$baseUrl + '/v1/dewater/supportApps',
				0,
				(res) => {
					if (res.data.code == 200) {
						var data = res.data.data;
						this.appList = data;
					}
				}
			)
		},
		getFAQ() {
			this.$uniRequest(
				this.$baseUrl + '/v1/faq/getFaqs',
				0,
				(res) => {
					if (res.data.code == 200) {
						var data = res.data.data;
						this.faqList = data.sort(this.compare('id'));
					}
				}
			)
		},
		compare(property) {
			return function(a, b) {
				var value1 = a[property];
				var value2 = b[property];
				return value1 - value2;
			}
		}
	}
}
